:root {
    --black-text-color: #131313;
    --white-text-color: #fcfcfc;
    --accent-color: rgba(248, 210, 78, 1);
    --time-color: #ababab;
    --footer-button: #7a7a7a;
    --background-color: #84a178;
    --button-color: #0a8817;
}

body {
    font-family: Montserrat;
    font-style: normal;
}
.is-body-hidden {
    overflow: hidden;
}
.visually-hidden {
    visibility: hidden;
    height: 0;
}
html {
    box-sizing: border-box;
}

ul,
ol {
    padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

ul {
    list-style: none;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

input,
button,
textarea,
select {
    font: inherit;
}

img {
    max-width: 100%;
    display: block;
    // height: auto;
    // width: 100%;
}
